.catalog-menu {
  max-width: 380px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 34px;
}

.catalog-menu__list {
  padding: 0;
  margin: 0;

  list-style: none;
}

.catalog-menu__link {
  display: block;
  padding-right: 20px;
  padding-left: 20px;

  color: inherit;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-weight: 800;
  line-height: 44px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  letter-spacing: -0.01em;
  border: 1px solid transparent;
  transition: all 0.2s ease-out;

  &:hover,
  &:focus {
    color: var(--basic-brown);
    border: 1px solid var(--basic-brown);
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: 768px) {
  .catalog-menu {
    max-width: none;
    padding-left: 30px;
    padding-right: 30px;
  }

  .catalog-menu__list {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (min-width: 1100px) {
  .catalog-menu {
    max-width: 1100px;
  }
}
