.form {
  position: relative;
  background-color: #eeeeee;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 0;
    height: 100%;
    background-color: #eeeeee;
    width: calc((100vw - 1100px) / 2);
  }

  &::before {
    left: calc((100vw - 1100px) / -2);
  }

  &::after {
    right: calc((100vw - 1100px) / -2);
  }
}

.form__inner {
  position: relative;
  top: -100px;

  max-width: 380px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 15px;
  padding-bottom: 45px;

  border: 2px solid var(--basic-black);
  background-color: #ffffff;
}

.form p {
  margin: 0;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.form label {
  padding-bottom: 10px;

  font-family: "Georgia", "Times New Roman", serif;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
  letter-spacing: 0.02em;
}

.form input {
  padding: 0 35px;
  padding-right: 20px;

  color: var(--basic-brown);
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 14px;
  line-height: 38px;

  border: 2px solid var(--basic-black);
  background-repeat: no-repeat;
  background-position: left 10px center, right 10px center;
}

.form input#day {
  background-image:
    url("../img/icon-calendar.png"),
    url("../img/icon-arrow.png");
}

.form input#date {
  background-image:
    url("../img/icon-clock.png"),
    url("../img/icon-arrow.png");
}

.form input#name {
  background-image:
    url("../img/icon-user.png");
}

.form input#address {
  background-image:
    url("../img/icon-geo.png"),
    url("../img/icon-arrow.png");
}

.form input#tel {
  background-image: url("../img/icon-mobile.png");
}

.form button {
  width: 100%;
  padding: 7px;
  margin-top: 10px;

  color: #ffffff;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  line-height: 38px;
  letter-spacing: 0.15em;
  text-transform: uppercase;

  border: none;
  border-bottom: 3px solid var(--basic-brown-dark);
  background-color: var(--basic-brown);
  outline: none;
  transition: all 0.2s ease-out;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: 768px) {
  .form {
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 50px;
    padding-right: 50px;
  }

  .form__inner {
    max-width: none;
    display: flex;
    flex-wrap: wrap;
    padding-left: 7%;
    padding-right: 7%;
  }

  .form__left {
    flex-grow: 10;
    margin-right: 7%;
  }

  .form__right {
    flex-grow: 9;
  }
}

@media (min-width: 1100px) {
  .form__inner {
    padding-left: 170px;
    padding-right: 170px;
  }

  .form__left {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: stretch;
  }

  .form__one-stroke {
    display: flex;
    justify-content: space-between;
  }

  .form input#day {
    width: 180px;
  }

  .form input#date {
    width: 130px;
  }
}
