.main-nav {
  max-width: 380px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 20px;
  text-align: center;
}

.main-nav__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-start;
  min-height: 57px;
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.main-nav__link {
  display: block;
  padding-top: 5px;
  padding-bottom: 10px;
  color: #ffffff;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s ease-out;

  &:hover,
  &:focus {
    color: var(--basic-brown);
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: 768px) {
  .main-nav {
    max-width: none;
    display: flex;
    justify-content: space-between;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 50px;
    padding-right: 50px;
  }

  .main-nav__logo {
    flex-grow: 1;
    padding-right: 20px;
  }

  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    align-items: center;
    justify-content: flex-end;
    margin-left: auto;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .main-nav__link {
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 20px;
  }

  .main-nav__link--active span {
    background-image: linear-gradient(to right, var(--basic-grey-dark) 60%, transparent 40%);
    background-position: left bottom;
    background-repeat: repeat-x;
    background-size: 6px 1px;
  }
}

@media (min-width: 1100px) {
  .main-nav {
    max-width: 1100px;
  }

  .main-nav__logo {
    flex-grow: 0;
    margin-left: 60px;
    margin-right: 20px;
  }
}
