.brands {
  padding-top: 30px;
  padding-bottom: 15px;
  background-color: var(--basic-black);
  background-image: url("../img/brands-bg.png");
  background-repeat: repeat;
}

.brands__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.brands__link {
  display: block;
  padding: 10px 0;
}

@media (min-width: 768px) {
  .brands__list {
    padding-right: 50px;
    padding-left: 50px;
    flex-direction: row;
  }

  .brands__item {
    margin-right: 40px;
  }

  .brands__item:last-child {
    margin-right: 0;
  }
}

@media (min-width: 1100px) {
  .brands {
    padding-top: 15px;
  }

  .brands__list {
    width: 1100px;
    margin-left: auto;
    margin-right: auto;
  }

  .brands__item {
    margin-right: 65px;
  }
}
