.advantage {
  min-height: 411px;

  color: #ffffff;

  background-color: var(--basic-brown);
  background-image: url("../img/advantage-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.advantage__text {
  margin: 0;
  padding: 20px;
  padding-top: 40px;
  text-align: center;
  line-height: 28px;
}

.advantage__text-italic {
  display: block;
  padding-bottom: 15px;

  font-family: "Georgia", "Times New Roman", serif;
  font-size: 28px;
  font-weight: bold;
}

.advantage__text-normal {
  padding-bottom: 15px;

  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 24px;
  text-transform: uppercase;

}

.advantage__text-bold {
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 800;
}

.advantage__text-resume {
  position: relative;
  display: block;
  width: max-content;
  margin-right: auto;
  margin-left: auto;
  padding-top: 20px;
  padding-bottom: 35px;

  font-family: "Georgia", "Times New Roman", serif;
  font-size: 20px;
  line-height: 38px;
  text-align: center;

  background-image: url("../img/advantage-line.png");
  background-repeat: no-repeat;
  background-position: center bottom;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 10px;
    right: 4px;
    height: 2px;

    background-color: #ffffff;
  }
}

@media (min-width: 768px) {
  .advantage__text {
    padding-top: 90px;
    line-height: 38px;
  }

  .advantage__text-italic {
    padding-bottom: 0;
  }

  .advantage__text-normal {}
  padding-bottom: 0;
}
