.page-header__top {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  max-width: 380px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.page-header__location {
  max-width: 137px;
  margin-left: auto;
  padding: 0;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;

  font-family: inherit;
  font-size: inherit;

  border: none;
  background-color: transparent;
  background-image: url("../img/icon-geo.png");
  background-repeat: no-repeat;
  background-position: left center;
  cursor: pointer;
  transition: all 0.2s ease-out;

  &:hover,
  &:focus {
    color: var(--basic-brown);
  }

  &:hover .page-header__location-text::before,
  &:focus .page-header__location-text::before {
    content: none;
    transition: all 0.2s ease-out;
  }

  &:active {
    opacity: 0.3;
    outline: none;
  }
}

.page-header__location-text {
  position: relative;
  display: block;
  padding-right: 20px;

  text-align: left;

  background-image: url("../img/icon-arrow.png");
  background-repeat: no-repeat;
  background-position: right center;

  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    right: 20px;
    height: 1px;
    background-color: var(--basic-black);
  }
}

.page-header__tel {
  padding: 10px;
  padding-left: 0;
  padding-right: 0;
  font-size: 17px;
  line-height: 18px;
  color: inherit;
  text-decoration: none;
  transition: all 0.3s ease-out;

  &:hover,
  &:focus {
    color: var(--basic-brown);
  }

  &:active {
    opacity: 0.3;
  }
}

.page-header__bottom {
  background-color: var(--basic-black);
}

@media (min-width: 768px) {
  .page-header__top {
    max-width: none;
    padding-left: 50px;
    padding-right: 50px;
  }

  .page-header__tel {
    margin-left: auto;
  }
}

@media (min-width: 1100px) {
  .page-header__top {
    max-width: 1100px;
  }

  .page-header__location {
    margin-left: 120px;
  }

  .page-header__tel {
    margin-left: 100px;
  }
}
