.preview {
  margin-bottom: 15px;
}

.preview__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.preview__item {
  margin-bottom: 7px;
}

.preview__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 140px;

  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  font-family: "Proxima Nova", "Arial", sans-serif;
  text-align: center;
}

.preview__item {
  max-width: 328px;
  min-height: 328px;
  margin-right: auto;
  margin-left: auto;
  background-repeat: no-repeat;
  background-position: center;
  background-color: var(--basic-brown);
}

.preview__item-1 {
  background-image: url("../img/preview-1.jpg");
}

.preview__item-2 {
  background-image: url("../img/preview-2.jpg");
}

.preview__item-3 {
  background-image: url("../img/preview-3.jpg");
}

.preview__item-4 {
  background-image: url("../img/preview-4.jpg");
}

.preview__item-5 {
  background-image: url("../img/preview-5.jpg");
}

.preview__item-6 {
  background-image: url("../img/preview-6.jpg");
}

.preview__title {
  display: block;
  font-size: 22px;
  line-height: 30px;
  font-weight: bold;
}

.preview__brand-name {
  display: block;
  font-family: "Georgia", "Times New Roman", serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 30px;
}

.preview__text {
  font-size: 12px;
  line-height: 30px;
}

.preview__bg {
  position: relative;
  margin-right: 20px;
  margin-left: 20px;
  padding: 10px 5px;

  &::before {
    background-color: var(--basic-brown);
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    mix-blend-mode: multiply;
  }
}

.preview__slogan {
  position: relative;
  z-index: 1;

  margin: 0;

  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
  text-transform: initial;
}

.preview__separator {
  position: relative;
  display: block;
  width: 116px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    width: 41px;
    height: 1px;
    background-color: #ffffff;
  }

  &::before {
    left: 0;
  }

  &::after {
    right: 0
  }
}

.preview__button {
  display: block;
  width: max-content;
  margin-top: 10px;
  padding: 10px 10px;
  border: 2px solid #ffffff;
}

.preview__text-tateossian {
  display: block;
  width: 230px;
  padding: 15px;

  line-height: 18px;
}

.preview__date {
  position: relative;
  display: block;
  padding: 15px;
  margin-top: 10px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    left: 50%;
    transform: translateX(-50%);
    width: 210px;
    height: 1px;
    background-color: #ffffff;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }
}

@media (min-width: 768px) {
  .preview__list {
    display: flex;
    flex-wrap: wrap;
    width: calc((328px * 2) + 7px);
    margin-right: auto;
    margin-left: auto;
  }

  .preview__item {
    width: 328px;
    margin-left: 0;
    margin-right: 7px;
  }

  .preview__item:nth-child(2n) {
    margin-right: 0;
  }

  .preview__item:nth-last-child(1),
  .preview__item:nth-last-child(2) {
    margin-bottom: 0;
  }
}

@media (min-width: 1100px) {
  .preview__list {
    width: 998px;
  }

  .preview__item:nth-child(2n) {
    margin-right: 7px;
  }

  .preview__item:nth-child(3n) {
    margin-right: 0;
  }

  .preview__item:nth-last-child(3) {
    margin-bottom: 0;
  }
}


