.appointment {
  padding-top: 70px;
  padding-bottom: 150px;
  padding-left: 20px;
  padding-right: 20px;

  font-family: "Proxima Nova", "Arial", sans-serif;
  text-align: center;

  background-image: url("../img/brands-bg.png");
  background-color: #eeeeee;
}

.appointment__text {
  display: block;

  font-size: 26px;
  font-weight: 800;
  line-height: 38px;
  text-transform: uppercase;

  background-color: transparent;
}

.appointment__question {
  position: relative;

  margin: 0;
  padding-bottom: 30px;

  font-size: 20px;
  line-height: 24px;

  &::before {
    position: absolute;
    content: "";
    width: 166px;
    height: 2px;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    background-color: var(--basic-black);
  }
}
