.user-list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding: 0;

  list-style: none;
}

.user-list__item {
  display: flex;
  align-items: center;
  width: 50%;

  background-repeat: no-repeat;
  background-position: left center;
}

.user-list__item--login {
  background-image: url("../img/icon-user.png");
}

.user-list__item--cart {
  background-image: url("../img/icon-cart.png");
}

.user-list__btn {
  display: flex;
  padding: 0;
  padding-left: 30px;
  padding-right: 10px;
  padding-top: 9px;
  padding-bottom: 11px;

  font-family: inherit;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.12em;

  border: none;
  background-color: transparent;
  transition: all 0.3s ease-out;
  cursor: pointer;

  &:hover,
  &:focus {
    color: var(--basic-brown);
  }

  &:active {
    opacity: 0.3;
    outline: none;
  }
}

.user-list__btn span {
  background-image: linear-gradient(to right, var(--basic-grey-dark) 60%, transparent 40%);
  background-position: left bottom;
  background-repeat: repeat-x;
  background-size: 6px 1px;
}

.user-list__btn-number {
  position: relative;
  display: block;
  padding-left: 5px;
  padding-right: 5px;

  color: var(--basic-brown);
  font-family: "PT Serif", "Arial", sans-serif;
  font-size: 14px;

  &::before {
    position: absolute;
    content: "";
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 1px;
    height: 12px;

    background-color: var(--basic-brown);
  }
}

@media (min-width: 768px) {
  .user-list {
    margin-left: auto;
    width: max-content;
  }
}
