.social {
  display: flex;
  margin: 0;
  margin-right: 10px;
  padding: 11px 0;

  list-style: none;
}

.social__item {
  border: 1px solid var(--basic-grey);
}

.social__item:not(:last-child) {
  border-right: none;
}

.social__svg {
  fill: var(--basic-grey);
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  transition: all 0.2s ease-out;

  &:active {
    background-color: var(--basic-grey);
  }

  &:hover .social__svg,
  &:focus .social__svg {
    fill: var(--basic-brown);
    transition: all 0.2s ease-out;
  }
}
