:root {
  --basic-grey-dark: #888888;
  --basic-grey: #c2c2c2;
  --basic-black: #1f1f1f;
  --basic-brown: #b98348;
  --basic-brown-dark: #8b5c28;
}

body {
  min-width: 320px;
  margin: 0;

  color: var(--basic-black);
  font-family: "Proxima Nova Cond", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.1em;

  background-color: var(--white);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
