.intro {
  margin-bottom: 34px;

  background-image: url("../img/intro-bg.jpg");
  background-position: center bottom;
  background-size: cover;
  background-color: var(--basic-brown);
}

.intro__inner {
  padding-top: 30px;
  padding-bottom: 30px;
}

.intro__text {
  position: relative;

  max-width: 380px;
  margin: 0;
  margin-right: auto;
  margin-left: auto;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;

  color: #ffffff;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 28px;
  line-height: 30px;
  text-align: center;

  &::before,
  &::after {
    position: absolute;
    content: "";
    width: 160px;
    height: 3px;
    left: 50%;
    transform: translateX(-50%);

    background-color: #ffffff;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }
}

@media (min-width: 768px) {
  .intro__inner {
    padding-top: 110px;
    padding-bottom: 110px;
  }

  .intro__text {
    max-width: 650px;
    padding-left: 50px;
    padding-right: 50px;

    font-size: 48px;
    line-height: 54px;
  }
}

@media (min-width: 1100px) {
  .intro__inner {
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
  }

  .intro__text {
    margin-left: 0;
  }

  .intro__inner {
    background-image: url("../img/intro-man.png");
    background-position: left 700px top;
    background-repeat: no-repeat;
  }
}


